<template>
  <div>
    <el-table
      stripe
      border
      highlight-current-row
      :header-cell-style="tableHeader"
      :max-height="maxHeight"
      :data="data">
      <template v-for="column in columns">
        <el-table-column
          v-if="column.operation"
          header-align="center"
          :key="column.prop"
          fixed="right"
          :label="column.label"
          :width="column.width"
          :align="column.align?column.align:'center'">
          <template slot-scope="scope">
            <el-tooltip
              class="item"
              effect="dark"
              placement="top"
              v-for="item in column.operations"
              :key="item.event"
              :content="item.text">
              <el-button
                size="small"
                type="text"
                @click="emitEvent(item.event, scope.row[rowKey])"
                :icon="item.icon">
              </el-button>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column
          v-else-if="['selection', 'index', 'expand'].includes(column.type)"
          header-align="center"
          :type="column.type"
          :key="column.prop"
          :prop="column.prop"
          :width="column.width"
          :label="column.label"
          :align="column.align?column.align:'center'">
        </el-table-column>
        <el-table-column
          v-else
          header-align="center"
          :key="column.prop"
          :prop="column.prop"
          :min-width="column.width"
          :label="column.label"
          :align="column.align?column.align:'center'">
          <template slot-scope="scope">
            <el-input
              size="small"
              v-if="scope.row.isEdit && column.type === 'input'"
              v-model="scope.row[column.prop]"/>
            <el-select
              size="small"
              v-else-if="scope.row.isEdit && column.type === 'select'"
              v-model="scope.row[column.prop]">
              <el-option
                v-for="option in column.options"
                :key="option.value"
                :value="option.value"
                :label="option.label">
              </el-option>
            </el-select>
            <span v-else>{{scope.row[column.prop]}}</span>
          </template>
        </el-table-column>
      </template>
    </el-table>
    <el-button
      :style="specialTableBtn"
      type="primary"
      icon="el-icon-plus"
      @click="addRecord">
      新增行
    </el-button>
  </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
  name: 'EditTable',
  props: {
    maxHeight: {
      type: Number,
      default: 450
    },
    columns: {
      type: Array,
      required: true
    },
    data: {
      type: Array,
      required: true
    },
    rowKey: {
      type: String,
      required: true
    },
    currentEdit: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      specialTableBtn: {
        width: '100%',
        marginTop: '5px',
        border: '1px dashed #15afff',
        background: '#fff',
        color: '#15afff',
        display: 'block'
      }
    }
  },
  computed: {
    ...mapState({
      tableHeader: state => state.app.tableHeader
    })
  },
  methods: {
    addRecord () {
      this.$emit('addRecord')
    },
    emitEvent (event, id) {
      this.$emit(event, id)
    }
  }
}
</script>

<style scoped>

</style>



